// Any variables that need to use Hugo configuration need to be declared here
// Colors
$primary: #f24088;
$black: #2f2f41;
$white: #ffffff;
$white-offset: #f6f7ff;
$grey: #5C5A5A;

// Fonts
$font-family-base: Source Sans Pro, Helvetica, Arial, sans-serif, -apple-system;
$font-family-heading: Playfair Display, serif, -apple-system;


// Variables - Override Bootstrap variables and set your own here
@import 'variables';

// Bootstrap
@import 'bootstrap/bootstrap-reboot';
@import 'bootstrap/bootstrap-grid';
@import 'bootstrap/bootstrap-utilities';
// @import 'bootstrap/bootstrap'; // Uncomment this line to import the entire Bootstrap library

// Libraries
@import 'libraries/hamburgers/hamburgers';

// Components
@import 'components/type';
@import 'components/page';
@import 'components/header';
@import 'components/footer';
@import 'components/sub-footer';
@import 'components/logo';
@import 'components/main-menu';
@import 'components/main-menu-mobile';
@import 'components/hamburger';
@import 'components/buttons';
@import 'components/call';
@import 'components/title';
@import 'components/content';
@import 'components/intro';
@import 'components/intro-image';
@import 'components/strip';
@import 'components/feature';
@import 'components/social';

// Pages
@import 'pages/home';
@import 'pages/team/team-summary';
@import 'pages/team/team-list';
@import 'pages/services/page-services-list';
@import 'pages/services/page-services-single';

body {
  font-size: 16px;
  line-height: 1.2;
  font-family: $font-family-base;
  @include media-breakpoint-up(md) {
    font-size: 16px;
    line-height: 1.3;
  }
}
